import { Vue } from 'vue-property-decorator'

import App from './app.vue'

Vue.config.productionTip = false

export class Charts {
  constructor(selector_: string, dataJSON_: string, width_?: number, height_?: number) {
    new Vue({
      el: selector_,
      components: { app: App },
      template: '<app :dataJSON="dataJSON" :width="width" :height="height" />',
      data: { dataJSON: dataJSON_, width: width_, height: height_ },
    })
  }
}
