








































import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import { ResizeObserver } from 'vue-resize'

import PieChart from './pie-chart.vue'
import BarChart from './bar-chart.vue'
import LineChart from './line-chart.vue'

interface Chart {
  labels: string | string[] | null
  datasets:
    | {
        label?: string | null
        backgroundColor: string | string[] | null
        data: any[]
      }[]
    | null
}

@Component({ components: { PieChart, BarChart, LineChart, ResizeObserver } })
export default class extends Vue {

  @Prop() dataJSON: string
  @Prop() width: number
  @Prop() height: number

  is_no_data = false
  is_rendered = false
  is_mobile = false
  pie_chart: Chart | null = null
  bar_chart: Chart | null = null
  line_chart: Chart | null = null
  data: any

  bar_chart_options: any = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  }
  line_chart_options: any = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  }

  handleResize() {
    this.is_mobile = window.innerWidth < 480
  }

  @Watch('dataJSON')
  rebuildChart() {
    this.buildChart()
    this.is_rendered = true
  }

  mounted() {
    this.buildChart()
    this.is_rendered = true
  }

  async buildChart() {
    this.data = JSON.parse(this.dataJSON)

    if (this.data.type === 'pie') {
      this.pie_chart = {
        labels: this.data.series.map((v_, i_) => {
          return v_.label || `series${i_+1}`
        }),
        datasets: [
          {
            backgroundColor: this.data.series.map(v_ => {
              return v_.color || 'white'
            }),
            data: this.data.data || [],
          },
        ],
      }
    } else if (this.data.type === 'bar') {
      this.bar_chart = {
        labels: this.data.xaxis.data,
        datasets: this.data.series.map((v_, i_) => {
          return {
            label: v_.label || `series${i_+1}`,
            backgroundColor: v_.color || 'white',
            data: this.data.data[i_] || [],
            hidden: v_.hidden || false,
          }
        }),
      }
      this.bar_chart_options.scales.xAxes = [
        {
          scaleLabel: {
            display: true,
            labelString: this.data.xaxis.label,
          }
        }
      ]
    } else if (this.data.type === 'line') {
      this.line_chart = {
        labels: this.data.xaxis.data,
        datasets: this.data.series.map((v_, i_) => {
          return {
            label: v_.label || `series${i_+1}`,
            borderColor: v_.color || 'white',
            data: this.data.data[i_] || [],
            hidden: v_.hidden || false,
            fill: false,
            lineTension: 0,
          }
        }),
      }
      this.line_chart_options.scales.xAxes = [
        {
          scaleLabel: {
            display: true,
            labelString: this.data.xaxis.label,
          }
        }
      ]
    } else {
      this.is_no_data = true
    }
  }
}
